<script lang="ts">
  import { onMount } from "svelte";
  import SkeletonLoader from "../common/SkeletonLoader.svelte";
  import { link } from "svelte-spa-router";
  import { formatDate } from "../../lib/datetime";
  import { getHTMLWithoutTags, getFirstImageUrl } from "../../lib/marked";
  import { convertLabelsToTags } from "../../lib/tags";
  import TagList from "../tags/TagList.svelte";

  export let issue = null;
  let thumbnail;

  onMount(() => {
    if (issue === null) {
      return;
    }

    thumbnail = getFirstImageUrl(issue.body);
  });
</script>

<div class="box">
  <div class="columns is-desktop">
    <div class="column">
      <div class="content">
        {#if issue}
          <a href={`/articles/${issue.number}`} use:link>
            <h2>{issue.title}</h2>
          </a>
        {:else}
          <h2><SkeletonLoader width={50} /></h2>
        {/if}

        <p class="article-content">
          {#if issue}
            {@html getHTMLWithoutTags(issue.body.substring(0, 100))}
          {:else}
            <SkeletonLoader />
          {/if}
        </p>

        <p>
          {#if issue}
            <span class="icon">
              <i class="fas fa-clock" />
            </span>{formatDate(issue.created_at)}
            <span class="icon">
              <i class="fas fa-comment" />
            </span>{issue.comments} comments
          {:else}
            <SkeletonLoader width={30} />
          {/if}
        </p>
      </div>
    </div>
    {#if issue}
      {#if thumbnail}
        <div class="column">
          <div class="is-pulled-right">
            <figure class="image is-128x128">
              <img
                class="article-image"
                src={thumbnail}
                alt="article thumbnail"
              />
            </figure>
          </div>
        </div>
      {/if}
    {:else}
      <div class="column">
        <div class="is-pulled-right">
          <figure class="image is-128x128">
            <SkeletonLoader />
          </figure>
        </div>
      </div>
    {/if}
  </div>

  {#if issue}
    <TagList tags={convertLabelsToTags(issue.labels)} />
  {:else}
    <SkeletonLoader />
  {/if}
</div>

<style>
  img.article-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
</style>
